<template>
  <div class="page-changeCity">
    <el-row>
      <province/>
    </el-row>
    <el-row>
      <hot-city title="热门城市:" :list="hotCity"/>
    </el-row>
    <el-row>
      <hot-city title="最近访问:" :list="rencentList"/>
    </el-row>
    <el-row>
      <categroy/>
    </el-row>
  </div>
</template>
<script>
import province from "@/components/changecity/province";
import hotCity from "@/components/changecity/hotCity";
import categroy from "@/components/changecity/categroy";


import Api from "@/api/index.js";
const api = new Api()
export default {

  created(){
   api.getHotCity().then((res)=>{
      this.hotCity = res.data.data.map((ele,index)=>{
          return ele.name
      })
   });
   api.getRecents().then((res)=>{
      this.rencentList = res.data.data.map((ele,index)=>{
          return ele.name
      })
   });
  
  
  },
  components: {
    province,
    hotCity,
    categroy
  },
  data() {
    return {
      hotCity: [],
      rencentList: [],
    };
  }
};
</script>
<style lang="scss">
@import "@/assets/css/changecity/iselect.scss";
</style>



