<template>
  <div :class="['choose-wrap',disable ?'disabled':'']" @click="show" v-document_click="show_active">
    <div class="choose">
      <span>{{value}}</span>
      <i class="el-icon-caret-bottom"></i>
      <div :class="['mt-content',{'active':show_key}]">
        <h2>{{title}}</h2>
        <div :class="['wrapper',className]">
          <div class="col" v-for="(v,i) in renderList" :key="i">
            <span
              @click="chosePri(item)"
              :class="['mt-item',{'active':item.name === value}]"
              v-for="(item,index) in v"
              :key="index"
            >{{item.name}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["list", "show_key",'value','title','disable','className'],
  computed:{
      renderList(){
        let col = Math.ceil(this.list.length / 12);
        let result = [];
        for(var i = 0;i < col;i++){
            var data  = this.list.slice(i*12,i*12+12);
            result.push(data)
        }
       
        return result
       
      }
  },
  data() {
    return {

    };
  },
  methods: {
    show(e) {
      if(!this.disable){
          this.$emit("provinceClick", true);
      }
      
      e.stopPropagation();
    },
    show_active() {
      this.$emit("provinceClick", false);
    },
    chosePri(data) {
      this.$emit("chose_pri", data);
      console.log(1)
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/assets/css/changecity/select.scss";
</style>



