<template>
  <div>
    <span>按省份选择：</span>
    <m-select
      :list="provinceList"
      title="省份"
      :value="province"
      :show_key="provinceKey"
      className="province"
      @provinceClick="province_click"
      @chose_pri="chosePri"/>
    <m-select
      :list="cityList"
       title="城市"
      :value="city"
      :show_key="cityKey"
      className="city"
      :disable="cityDisable"
      @provinceClick="city_click"
      @chose_pri="choseCity"/>

    <span>直接搜索：</span>
    <el-select v-model="searchWord" placeholder="请输入关键词">
      <el-option v-for="item in serachList" :key="item" :value="item"></el-option>
    </el-select>
    
  </div>
</template>
<script>
import MSelect from "@/components/changecity/select";
import Api from "@/api/index.js";
const api = new Api();
export default {
  components: {
    MSelect
  },
  created(){
    api.getProvinceList().then((res)=>{
       
       this.provinceList = res.data.data.map((ele)=>{
              ele.name = ele.provinceName;
              return ele      
       });
    })
  },
  data() {
    return {
      searchWord: "",
      serachList: ["山东", "甘肃", "江苏", "北京", "云南", "海南", "浙江"],
      provinceKey: false,
      cityKey: false,
      provinceList:[],
      cityList:[],
      city:'城市',
      province:'省份',
      cityDisable:true
    };
  },
  methods: {
    province_click(flag) {
      this.provinceKey = flag;
      if (this.provinceKey) {
        this.cityKey = false;
      }
    },
    city_click(flag) {
      this.cityKey = flag;
      if (this.cityKey) {
        this.provinceKey = false;
      }
    },
    chosePri(data) {
      this.province = data.name;
      this.cityDisable = false;
      this.cityList = data.cityInfoList

    },
    choseCity(data) {
      this.city = data.name
      this.$store.state.province = data.name
    }
  }
};
</script>
<style lang="scss">
</style>



